import React, { Component } from 'react';
import marked from 'marked';
import './bottom.css';
class ComponentGenericPageBottom extends Component
{
    constructor(props)
    {
        super(props);
        window.addEventListener("resize", () =>
        {
            this.setState({
                IgnoreMe: "NULL"
            });
        });
        this.oldArg = "IGNORETHISSTRINGFONOW";
    }
    componentWillMount()
    {
        this.setState({
            elements: ""
        });
    }
    render()
    {
        if (this.state.elements === "" || this.oldArg !== this.props.args)
        {
            this.oldArg = this.props.args;
            window.dispatchEvent(new CustomEvent("_event_onRequestFile",
            {
                detail:
                {
                    path: this.props.args + "bottom.md",
                    onLoaded: (home) =>
                    {
                        let renderer = new marked.Renderer();
                        renderer.heading = (text, level) =>
                        {
                            return '<h' + level + ">" + text + "</h" + level + ">";
                        }
                        marked.setOptions({
                            renderer: renderer,
                            gfm: true,
                            tables: true,
                            breaks: false,
                            pedantic: false,
                            sanitize: false,
                            smartLists: false,
                            smartypants: false
                        });
                        let md = home;
                        while (md.includes("```"))
                        {
                            md = md.replace("```", "<code id='codeblock' style='display: block; color: rgba(200, 200, 150, 0.75); background-color: rgba(0, 0, 0, 0.25); border-radius: 10px; white-space: pre-wrap; padding: 25px; width: 100%;'>");
                            md = md.replace("```", "</code>");
                        }
                        while (md.includes("`"))
                        {
                            md = md.replace("`", "<code style='display: inline-block; background-color: rgba(0, 0, 0, 0); white-space: pre-wrap;'>");
                            md = md.replace("`", "</code>");
                        }
                        md = marked(md);
                        while (md.includes("&lt;"))
                        {
                            md = md.replace("&lt;", "<");
                        }
                        while (md.includes("&gt;"))
                        {
                            md = md.replace("&gt;", ">");
                        }
                        while (md.includes("&#39;"))
                        {
                            md = md.replace("&#39;", "'");
                        }
                        let first = true;
                        while (md.includes("<h1>"))
                        {
                            if (first)
                            {
                                md = md.replace("<h1>", "<div class='jumbotron' style='background-color: rgb(20, 21, 22); color: white; border-radius: 0px; margin-bottom: 0px; margin-left: 10%; margin-right: 10%; padding-top: 15px; padding-bottom: 15px;'><h1 class='display-3' style='font-size: 40px;'>");
                                first = false;
                            }
                            else
                            {
                                md = md.replace("<h1>", "<div class='jumbotron' style='background-color: rgba(0, 0, 0, 0.5); color: white; border-radius: 0px; margin-bottom: 0px; margin-top: 0px; margin-left: 10%; margin-right: 10%; padding-top: 15px; padding-bottom: 15px;'><h1 class='display-3' style='font-size: 40px;'>");
                            }
                        }
                        while (md.includes("</h1>"))
                        {
                            md = md.replace("</h1>", "</h1 ></div>");
                        }
                        while (md.includes("<p>"))
                        {
                            md = md.replace("<p>", "<div class='jumbotron' style='background-color: rgba(0, 0, 0, 0.25); color: rgba(255, 255, 255, 0.75); border-radius: 0px; text-align: justify; margin-bottom: 0px; margin-top: 0px; padding-top: 5px; padding-bottom: 1px; margin-left: 10%; margin-right: 10%;'><p class='lead' style='display: inline-block; padding-right: 0px; margin-right: 0px; width: 100%;'>");
                        }
                        while (md.includes("</p>"))
                        {
                            md = md.replace("</p>", "</p ></div>");
                        }
                        while (md.includes("<h2>"))
                        {
                            md = md.replace("<h2>", "<div class='jumbotron' style='background-color: rgba(0, 0, 0, 0.25); color: rgba(255, 255, 255, 0.75); border-radius: 0px; text-align: justify; margin-bottom: -8px; padding-top: 15px; padding-bottom: 0px; margin-left: 10%; margin-right: 10%;'><h1 class='display-4' style='font-size: 30px;'>");
                        }
                        while (md.includes("</h2>"))
                        {
                            md = md.replace("</h2>", "</h1></div>");
                        }
                        while (md.includes("<h3>"))
                        {
                            md = md.replace("<h3>", "<div class='jumbotron' style='background-color: rgba(0, 0, 0, 0.25); color: rgba(255, 255, 255, 0.75); border-radius: 0px; text-align: justify; margin-bottom: -8px; padding-top: 15px; padding-bottom: 0px; margin-left: 10%; margin-right: 10%;'><h1 class='display-4' style='font-size: 20px; font-weight: bold;'>");
                        }
                        while (md.includes("</h3>"))
                        {
                            md = md.replace("</h3>", "</h1></div>");
                        }
                        while (md.includes("<ul>"))
                        {
                            md = md.replace("<ul>", "<div class='jumbotron' style='background-color: rgba(0, 0, 0, 0.25); color: rgba(255, 255, 255, 0.75); border-radius: 0px; text-align: justify; margin-bottom: -16px; margin-top: 0px; padding-top: 0px; padding-bottom: 0px; margin-left: 10%; margin-right: 10%;'><ul >");
                        }
                        while (md.includes("</ul>"))
                        {
                            md = md.replace("</ul>", "</ul ></div>");
                        }
                        while (md.includes("<table>"))
                        {
                            md = md.replace("<table>", "<div class='jumbotron' style='background-color: rgba(0, 0, 0, 0.25); color: rgba(255, 255, 255, 0.75); border-radius: 0px; text-align: justify; margin-bottom: 0px; margin-top: 0px; padding-top: 5px; padding-bottom: 15px; margin-left: 10%; margin-right: 10%;'><table >");
                        }
                        while (md.includes("</table>"))
                        {
                            md = md.replace("</table>", "</table ></div>");
                        }
                        this.setState({
                            elements: md
                        });
                    }
                }
            }));
        }    
        return (
            <div style={{ backgroundImage: "url('" + this.props.args + "bottom.jpg')", minHeight: window.innerHeight - 56 }}>
                <div dangerouslySetInnerHTML={{ __html: this.state.elements }} />
            </div>    
        );
    }
}
export default ComponentGenericPageBottom;


