import React, { Component } from 'react';
import Background from './top.jpg';
import { Jumbotron, Button } from 'reactstrap';

class ComponentHomeTop extends Component
{
    constructor(props)
    {
        super(props);
        this._render_containers = this._render_containers.bind(this);
        this._render_leftContainer = this._render_leftContainer.bind(this);
        this._render_rightContainer = this._render_rightContainer.bind(this);
        this._render_headerContainer = this._render_headerContainer.bind(this);
        this._render_messageContainer = this._render_messageContainer.bind(this);
        window.dispatchEvent(new CustomEvent("_event_onRequestFile",
        {
            detail:
            {
                path: "/resources/home/top.json",
                onLoaded: (hometop) =>
                {
                    hometop = JSON.parse(hometop);
                    this.setState({
                        title: hometop.title,
                        titleSize: hometop.titleSize,
                        subtitle: hometop.subtitle,
                        subtitleSize: hometop.subtitleSize,
                        spacing: hometop.spacing,
                        containers: hometop.containers
                    });
                }
            }
        }));
        window.addEventListener("resize", () =>
        {
            this.setState({
                IgnoreMe: "NULL"
            });
        });
    }
    componentWillMount()
    {
        this.setState({
            title: "",
            titleSize: 70,
            subtitle: "",
            subtitleSize: 25,
            spacing: 0,
            containers: []
        });
    }
    _render_headerContainer(container)
    {
        return (
            <Jumbotron style={{ backgroundColor: "rgba(0, 0, 0, 0.5)", color: "white", borderRadius: 0, textAlign: "center", margin: 0, marginLeft: "10%", marginRight: "10%", padding: 0}}>
                <p style={{ padding: 10, fontSize: 25 }} className="lead">{container.name}</p>
            </Jumbotron>
        );
    }
    _render_leftContainer(container)
    {
        let percentage = this.state.spacing;
        let btns = [];
        let i = 0;
        Object.keys(container.children).forEach((key) =>
        {
            let clickHandler = (event) =>
            {
                window.location = container.children[key];
            }
            if (i === 0)
            {
                i = 1;
                btns.unshift(<Button onClick={clickHandler} color={container.color} outline style={{ borderLeft: 0, borderTop: 0, borderBottom: 0, borderRadius: 0 }}>{key}</Button>);
            }
            else
            {
                btns.unshift(<Button onClick={clickHandler} color="secondary" outline style={{ borderRight: 0, borderLeft: 0, borderTop: 0, borderBottom: 0, borderRadius: 0 }}>{key}</Button>);
            }
        });
        return (
            <Jumbotron style={{ textAlign: "right", backgroundColor: "rgba(0, 0, 0, 0)", maxWidth: percentage + "%", marginBottom: 0, marginTop: 0, padding: 10 }}>
                <Button onClick={(e) => { window.location = container.href; }} color={container.color} outline style={{ fontSize: 35, borderLeft: 0, borderTop: 0, borderBottom: 0, borderRadius: 0 }}>{container.name}</Button>
                <p style={{ margin: 0 }}></p>
                {btns}
            </Jumbotron>
        );
    }
    _render_rightContainer(container)
    {
        let percentage = this.state.spacing;
        let btns = [];
        let i = 0;
        Object.keys(container.children).forEach((key) =>
        {
            let clickHandler = (event) =>
            {
                window.location = container.children[key];
            }
            if (i === 0)
            {
                i = 1;
                btns.push(<Button onClick={clickHandler} color={container.color} outline style={{ borderRight: 0, borderTop: 0, borderBottom: 0, borderRadius: 0 }}>{key}</Button>);
            }
            else
            {
                btns.push(<Button onClick={clickHandler} color="secondary" outline style={{ borderRight: 0, borderLeft: 0, borderTop: 0, borderBottom: 0, borderRadius: 0 }}>{key}</Button>);
            }
        });
        return (
            <Jumbotron style={{ textAlign: "left", backgroundColor: "rgba(0, 0, 0, 0)", maxWidth: percentage + "%", marginLeft: window.innerWidth * (1 - (percentage / 100)), marginBottom: 0, marginTop: 0, padding: 10 }}>
                <Button onClick={(e) => { window.location = container.href; }} color={container.color} outline style={{ fontSize: 35, borderRight: 0, borderTop: 0, borderBottom: 0, borderRadius: 0 }}>{container.name}</Button>
                <p style={{ margin: 0 }}></p>
                {btns}
            </Jumbotron>
        );
    }
    _render_messageContainer(container)
    {
        let bg = "rgba(0, 0, 0, 0.25)";
        if (container.alert === "warning")
        {
            bg = "rgba(100, 0, 0, 0.5)";
        }
        else if (container.alert === "reminder")
        {
            bg = "rgba(100, 100, 0, 0.5)";
        }
        else if (container.alert === "success")
        {
            bg = "rgba(0, 100, 0, 0.5)";
        }
        return (
            <Jumbotron style={{ backgroundColor: bg, color: "white", borderRadius: 0, textAlign: container.align, margin: 0, marginLeft: "10%", marginRight: "10%", padding: 0}}>
                <p style={{ fontFamily: "monospace", marginLeft: 25, marginRight: 25, padding: 10, backgroundColor: "rgba(0, 0, 0, 0)", color: "rgba(200, 200, 200, 1)" }} className="lead">{container.message}</p>
            </Jumbotron>
        );
    }
    _render_containers()
    {
        let containers = [];
        let j = 0;
        for (let i = 0; i < this.state.containers.length; i++)
        {
            if (this.state.containers[i].type === "group")
            {
                if (j % 2 === 0)
                {
                    containers.push(this._render_leftContainer(this.state.containers[i]));
                }
                else if (j % 2 === 1)
                {
                    containers.push(this._render_rightContainer(this.state.containers[i]));
                }
                j++;
            }
            else if (this.state.containers[i].type === "header")
            {
                containers.push(this._render_headerContainer(this.state.containers[i]));
            }
            else if (this.state.containers[i].type === "message")
            {
                containers.push(this._render_messageContainer(this.state.containers[i]));
            }
        }
        containers.push(<div style={{ paddingBottom: 30 }}/>)
        return (
            <div>{containers}</div>
        );
    }
    render()
    {
        return (
            <div className="bg-inverse" style={{ backgroundImage: "url(" + Background + ")", backgroundPosition: "top", backgroundRepeat: "repeat-y", minHeight: window.innerHeight }}>
                <Jumbotron style={{ backgroundColor: "rgba(0, 0, 0, 0.5)", borderRadius: "0", paddingBottom: 5, paddingLeft: 100, paddingRight: 100 }}>
                    <h1 className="display-1" style={{
                        color: "rgba(255, 255, 255, 1)",
                        fontFamily: "Monospace",
                        src: "url(http://uncutbeatz.com/clear/fonts/AGENCYR.TTF.ttf)",
                        fontStyle: "normal",
                        fontSize: this.state.titleSize
                    }}>{this.state.title}</h1>
                    <p className="lead" style={{ fontSize: this.state.subtitleSize, textAlign: "right", color: "rgba(255, 255, 255, 0.75)" }}>{this.state.subtitle}</p>
                </Jumbotron>
                {this._render_containers()}
            </div>
        );
    }
}
export default ComponentHomeTop;


