import React, { Component } from 'react';
import { Navbar, NavbarBrand, Nav, NavItem, NavLink, ButtonGroup, Button } from 'reactstrap';

import ComponentHomeTop from './components/home/top.jsx';
import ComponentHomeBottom from './components/home/bottom.jsx';
import ComponentGenericPageTop from './components/genericpage/top.jsx';
import ComponentGenericPageBottom from './components/genericpage/bottom.jsx';

class App extends Component
{
    constructor(props)
    {
        super(props);
        this._render_navigation = this._render_navigation.bind(this);
        this._event_onPop = this._event_onPop.bind(this);
        this.switchToComponent = this.switchToComponent.bind(this);
        window.dispatchEvent(new CustomEvent("_event_onRequestFile",
        {
            detail:
            {
                path: "/resources/navigation.json",
                onLoaded: (navigation) =>
                {
                    navigation = JSON.parse(navigation);
                    this.NavigationKeys = {};
                    this.NavigationArguments = {};
                    Object.keys(navigation.links).forEach((key) =>
                    {
                        this.NavigationKeys[key.toLowerCase()] = navigation.links[key][2];
                        if (navigation.links[key][3])
                        {
                            this.NavigationArguments[key.toLowerCase()] = navigation.links[key][3];
                        }
                    });
                    Object.keys(navigation.buttons).forEach((key) =>
                    {
                        this.NavigationKeys[key.toLowerCase()] = navigation.buttons[key][2];
                        if (navigation.buttons[key][3])
                        {
                            this.NavigationArguments[key.toLowerCase()] = navigation.buttons[key][3];
                        }
                    });
                    this.setState({
                        title: navigation.title,
                        links: navigation.links,
                        buttons: navigation.buttons,
                        default: navigation.default,
                        titleTree: navigation.titleTree
                    });
                    if (window.location.pathname === "/")
                    {
                        this.switchToComponent(navigation.default);
                    }
                    else
                    {
                        let p = window.location.pathname.substring(1);
                        this.switchToComponent(p);
                    }
                }
            }
        }));
        this.initial = true;
        window.onpopstate = this._event_onPop;
    }
    _event_onPop(e)
    {
        this.initial = true;
        if (window.location.pathname === "/")
        {
            this.switchToComponent(this.state.default);
        }
        else
        {
            let p = window.location.pathname.substring(1);
            console.log(p);
            this.switchToComponent(p);
        }
    }
    componentWillMount()
    {
        this.setState({
            title: "",
            links: {},
            buttons: {},
            selected: "",
            selectedComponent: "",
            titleTree: ""
        });
    }
    switchToComponent(activeKey)
    {
        activeKey = activeKey.toLowerCase();
        let componentName = this.NavigationKeys[activeKey];
        this.setState({
            selected: activeKey,
            selectedComponent: componentName
        });
        if (!window.location.pathname.includes("/" + activeKey))
        {
            if (this.initial)
            {
                window.history.replaceState({}, "", "/" + activeKey);
                this.initial = false;
            }
            else
            {
                window.history.pushState({}, "", "/" + activeKey);
            }
        }
        else
        {
            if (this.replace)
            {
                window.history.pushState({}, "", "/" + activeKey);
                this.replace = false;
            }
        }
        let title = activeKey.substring(0, 1).toUpperCase() + activeKey.substring(1);
        document.title = this.state.titleTree + title;
    }
    _render_navigation()
    {
        let items = {
            left: [],
            right: []
        };
        let buttons = [];
        Object.keys(this.state.links).forEach((key) =>
        {
            if (!key.includes("/"))
            {
                let hrefType = this.state.links[key][1];
                let href = "#";
                if (hrefType === "link")
                {
                    href = this.state.links[key][2];
                }
                let clickLinkHandler = () =>
                {
                    if (hrefType === "component")
                    {
                        this.replace = true;
                        this.switchToComponent(key);
                    }
                };
                if (key.toLowerCase() === this.state.selected)
                {
                    items[this.state.links[key][0]].push(<NavItem><NavLink href={href} onClick={clickLinkHandler} active>{key}</NavLink></NavItem>);
                }
                else
                {
                    items[this.state.links[key][0]].push(<NavItem><NavLink href={href} onClick={clickLinkHandler}>{key}</NavLink></NavItem>);
                }
            }    
        });
        Object.keys(this.state.buttons).forEach((key) =>
        {
            let hrefType = this.state.buttons[key][1];
            let clickButtonHandler = () =>
            {
                if (hrefType === "component")
                {
                    this.switchToComponent(key);
                }
                else if (hrefType === "link")
                {
                    window.location = this.state.buttons[key][2];
                }
            };
            buttons.push(<Button outline color={this.state.buttons[key][0]} onClick={clickButtonHandler}>{key}</Button>);
        });
        return (
            <Navbar style={{ backgroundColor: "rgba(0, 0, 0, 0.5)" }} inverse toggleable fixed="bottom" className="sticky-top">
                <NavbarBrand style={{ color: "rgba(250, 250, 250, 0.75)" }}>{this.state.title}</NavbarBrand>
                <Nav className="mr-auto" navbar>
                    {items["left"]}
                </Nav>
                <Nav className="ml-auto" navbar>
                    {items["right"]}
                    <ButtonGroup>
                        {buttons}
                    </ButtonGroup>
                </Nav>
            </Navbar>
        );
    }
    componentTop()
    {
        let args = "";
        if (this.NavigationArguments && this.NavigationArguments[this.state.selected])
        {
            args = this.NavigationArguments[this.state.selected];
        }
        return {
            "component.home": <ComponentHomeTop />,
            "component.genericpage": <ComponentGenericPageTop args={args} />
        };
    }
    componentBottom()
    {
        let args = "";
        if (this.NavigationArguments && this.NavigationArguments[this.state.selected])
        {
            args = this.NavigationArguments[this.state.selected];
        }
        return {
            "component.home": <ComponentHomeBottom />,
            "component.genericpage": <ComponentGenericPageBottom args={args} />
        }
    }
    render()
    {
        return (
            <div className="App">
                {this.componentTop()[this.state.selectedComponent]}
                {this._render_navigation()}
                {this.componentBottom()[this.state.selectedComponent]}
            </div>
        );
    }
}
export default App;


