import React, { Component } from 'react';
import Background from './bottom.jpg';
import { Jumbotron } from 'reactstrap';

class ComponentHomBottom extends Component
{
    constructor(props)
    {
        super(props);
        this._render_containers = this._render_containers.bind(this);
        this._render_header = this._render_header.bind(this);
        this._render_message = this._render_message.bind(this);
        window.dispatchEvent(new CustomEvent("_event_onRequestFile",
        {
            detail:
            {
                path: "/resources/home/bottom.json",
                onLoaded: (homebottom) =>
                {
                    homebottom = JSON.parse(homebottom);
                    this.setState({
                        containers: homebottom.containers
                    });
                }
            }
        }));
    }
    componentWillMount()
    {
        this.setState({
            containers: []
        });
    }
    _render_header(container)
    {
        return (
            <Jumbotron style={{ backgroundColor: "rgba(0, 0, 0, 0.5)", color: "white", borderBottomLeftRadius: 0, borderBottomRightRadius: 0, borderTopLeftRadius: 25, borderTopRightRadius: 25, marginBottom: 0, marginLeft: "10%", marginRight: "10%", paddingBottom: 15 }}>
                <h1 style={{ width: "100%" }}className="display-4" dangerouslySetInnerHTML={{ __html: container.header }}/>
            </Jumbotron>
        );
    }
    _render_message(container)
    {
        return (
            <Jumbotron style={{ backgroundColor: "rgba(0, 0, 0, 0.25)", color: "rgba(255, 255, 255, 0.75)", borderBottomLeftRadius: 25, borderBottomRightRadius: 25, borderTopLeftRadius: 0, borderTopRightRadius: 0, textAlign: "justify", marginBottom: 0, paddingTop: 15, paddingBottom: 15, marginLeft: "10%", marginRight: "10%" }}>
                <p className="lead" dangerouslySetInnerHTML={{ __html: container.message }}/>
            </Jumbotron>
        );
    }
    _render_containers()
    {
        let itms = [];
        itms.push(<div style={{ height: 20 }}></div>)
        for (let i = 0; i < this.state.containers.length; i++)
        {
            itms.push(this._render_header(this.state.containers[i]));
            itms.push(this._render_message(this.state.containers[i]));
            if (i !== this.state.containers.length - 1)
            {
                itms.push(<div style={{ height: 20 }}></div>)
            }
        }
        itms.push(<div style={{ height: 20 }}></div>)
        return itms;
    }
    render()
    {
        return (
            <div style={{ backgroundImage: "url(" + Background + ")" }}>
                {this._render_containers()}
            </div>
        );
    }
}
export default ComponentHomBottom;


